import React, {useState, useEffect, } from 'react'
import "./NavBar.css"
import IconButton from "@mui/material/IconButton";
import MenuIcon from "@mui/icons-material/Menu";
import { NavLink } from 'react-router-dom';
import { SwipeableDrawer } from '@mui/material';
import ClearOutlinedIcon from '@mui/icons-material/ClearOutlined';
import List from '@mui/material/List';
import ListItem from "@mui/material/ListItem";
import { withStyles } from "@mui/styles";

const MenuItem = withStyles({
    root: {
      justifyContent: "flex-end"
    }
})(ListItem);

const NavBar = () => {

    // Screen width setter
    const [screenWidth, setScreenWidth] = useState(window.innerWidth)

    // Open State Setter
    const [open, setOpen] = useState(false);

    //Screen Width Listener
    useEffect(() => {

        const changeWidth = () => {
            setScreenWidth(window.innerWidth);
        }

        window.addEventListener('resize', changeWidth)

        return () => {
            window.removeEventListener('resize', changeWidth)
        }

    }, [])

    const navigationLinks = [
        { name: "Home", href: "/" },
        { name: "About", href: "/about" },
        { name: "Work", href: "/work" },
        { name: "Contact", href: "/contact" },
    ];

    return (

        <div className = "container">

            <header>

            {/* Desktop Screen */}

                <nav className = "navbar">
                    <a href = "/" className = "nav-branding"> Michael Lee </a>
                {(screenWidth > 800) && (

                    <ul className = "nav-menu">
                        <li className = "nav-item">
                              <NavLink
                                    to= "/"
                                    className="nav-link"
                              >
                                Home
                             </NavLink>
                        </li>
                        <li className = "nav-item">
                            <NavLink
                                    to= "/about"
                                    className="nav-link"
                              >
                                About
                             </NavLink>
                        </li>
                        <li className = "nav-item">
                             <NavLink
                                    to= "/work"
                                    className="nav-link"
                              >
                                Work
                             </NavLink>
                        </li>
                        <li className = "nav-item">
                             <NavLink
                                    to= "/contact"
                                    className="nav-link"
                              >
                                Contact
                             </NavLink>
                        </li>
                    </ul>
                )}

                {(screenWidth < 800) && (
                    <div className = "mobile-menu">
                        <IconButton onClick={() => setOpen(true)}>
                            <MenuIcon className = "menu-icon">
                            </MenuIcon>
                        </IconButton>
                <SwipeableDrawer
                    anchor="top"
                    open={open}
                    onOpen={() => setOpen(true)}
                    onClose={() => setOpen(false)}
                >
                    {/* Close button */}

                    <div
                        onClick={() => setOpen(false)}
                        onKeyPress={() => setOpen(false)}
                        role="button"
                        tabIndex={0}
                    >
                        <IconButton className = "x-position">
                            <ClearOutlinedIcon className = "x-icon"/>
                        </IconButton>
                    </div>

                    {/* List of Items in Dropdown */}

                    <List>   {/* Try to list all items at once */}

                        {navigationLinks.map((item) => (
                            <ListItem key={item.name} style={{ justifyContent: "center" }}>
                                <NavLink
                                    to= {item.href}
                                    className="mobile-nav-element"
                                    onClick={() => setOpen(false)}
                                >
                                    {item.name}
                                </NavLink>
                            </ListItem>
                        ))}
                    </List>
                </SwipeableDrawer>
                </div>

                )}

                </nav>
            </header>
        </div>
    );
}

export default NavBar;
